* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  }
  .container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .box {
  width: 90%;
  height: 90vh;
  background-color: #a5bbdd;
  margin: auto;
  border-radius: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  }
  .inputData {
  padding-top: 20px;
  }
  .inputData input {
  padding: 10px;
  width: 90%;
  outline: none;
  border: none;
  font-size: 20px;
  border-radius: 10px;
  }
   i {
  font-size: 60px;
  margin-right: 1rem;
  }
  .info {
  margin-top: 5%;
  }
  .info .temp {
  font-size: 30px;
  margin: 20px 0 20px 0;
  }
  .info h3 {
  font-size: 20px;
  font-weight: 500;
  }
  .box2{
    font-size: 40px;
    color: black;
    display: flex;
    padding: 10px;
    align-items: center;
    margin-top: 80px;
    gap: 80px;
    justify-content: center;
    font-weight: 400;
    opacity: 0.8;
  }
  .humidity{
   height: 150px;
   width: 150px;
   padding: 30px 0;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
    border-radius: 0.5rem;
    background-color: #eee;
    font-size: 30px;
    
  }
  .feelslike{
    height: 150px;
   width: 150px;
   padding: 30px 0;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
    border-radius: 0.5rem;
    background-color: #eee;
    font-size: 30px;
  }
  .pressure{
    height: 150px;
    width: 150px;
    padding: 30px 0;
     box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
     border-radius: 0.5rem;
     background-color: #eee;
     font-size: 30px;
  }

  .box::after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 90%;
  height: 90%;
  z-index: 11;
  transform: translate3d(0, 0, 0);
  }
  @keyframes drift {
  from {
  transform: rotate(0deg);
  }
  from {
  transform: rotate(360deg);
  }
  }
